export default{
	props:["show"],
	data(){
		return{
			currentDate: new Date().getTime(),
			maxDate:"",
			isSelect:false,
			showPopup:false,
			tipText:""	
		}
	},
	methods:{
		selcetChange(event){
			this.isSelect = event.detail;
			if(!this.isSelect){
				this.tipText = "选择预约接种日期";
				this.maxDate = "";
				this.minDate = new Date().getTime();;
			}else{
				this.tipText = "选择实际接种日期";
				this.maxDate = new Date().getTime();
				this.minDate = "";
			}
		},
		setTime(event){
			this.currentDate = event.detail;
		},
		getTime(value){
			console.log(value);
		}
	},
	watch:{
		show:function(value){
			this.showPopup = value;
		}
	}
}
